import React from 'react';
import { Navigate, useLocation } from 'react-router-dom';
import { useAuth } from '@/context/AuthContext';
import { usePermissions } from '@/hooks/usePermissions';
// Removed Loading import - pages handle their own loading states
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { AlertTriangle } from 'lucide-react';

interface PermissionRouteProps {
  children: React.ReactNode;
  
  // Permission-based access
  permission?: string;
  permissions?: string[];
  requireAll?: boolean;
  
  // Role-based access
  role?: string;
  roles?: string[];
  requireAllRoles?: boolean;
  
  // Page-based access
  page?: string;
  
  // Super admin access
  requireSuperAdmin?: boolean;
  
  // Redirect paths
  redirectTo?: string;
  loginRedirect?: string;
  
  // Custom access denied component
  accessDeniedComponent?: React.ReactNode;
}

const PermissionRoute: React.FC<PermissionRouteProps> = ({
  children,
  permission,
  permissions,
  requireAll = false,
  role,
  roles,
  requireAllRoles = false,
  page,
  requireSuperAdmin = false,
  redirectTo = '/dashboard',
  loginRedirect = '/login',
  accessDeniedComponent
}) => {
  const { isLoggedIn, loading: authLoading } = useAuth();
  const location = useLocation();
  const {
    hasPermission,
    hasAnyPermission,
    hasAllPermissions,
    hasRole,
    hasAnyRole,
    isSuperAdmin,
    canAccessPage,
    loading: permissionsLoading
  } = usePermissions();

  // Removed full-page loading - pages handle their own loading states with skeleton loading
  // If still loading auth/permissions, render children and let them handle loading

  // Redirect to login if not authenticated (only if auth check is complete)
  if (!authLoading && !isLoggedIn) {
    return <Navigate to={loginRedirect} state={{ from: location }} replace />;
  }

  // If still loading, render children (they will show skeleton loading)
  if (authLoading || permissionsLoading) {
    return <>{children}</>;
  }

  // Check access permissions
  const checkAccess = (): boolean => {
    // Super admin check first
    if (requireSuperAdmin) {
      return isSuperAdmin;
    }

    // If super admin and no specific requirements, grant access
    if (isSuperAdmin && !permission && !permissions && !role && !roles && !page) {
      return true;
    }

    // Check single permission
    if (permission && !hasPermission(permission)) {
      return false;
    }

    // Check multiple permissions
    if (permissions && permissions.length > 0) {
      const hasAccess = requireAll 
        ? hasAllPermissions(permissions)
        : hasAnyPermission(permissions);
      
      if (!hasAccess) return false;
    }

    // Check single role
    if (role && !hasRole(role)) {
      return false;
    }

    // Check multiple roles
    if (roles && roles.length > 0) {
      const hasAccess = requireAllRoles
        ? roles.every(r => hasRole(r))
        : hasAnyRole(roles);
      
      if (!hasAccess) return false;
    }

    // Check page access
    if (page && !canAccessPage(page)) {
      return false;
    }

    return true;
  };

  const hasAccess = checkAccess();

  // If access is denied, show custom component or default access denied page
  if (!hasAccess) {
    if (accessDeniedComponent) {
      return <>{accessDeniedComponent}</>;
    }

    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <Card className="w-full max-w-md">
          <CardHeader className="text-center">
            <div className="mx-auto mb-4 flex h-12 w-12 items-center justify-center rounded-full bg-red-100">
              <AlertTriangle className="h-6 w-6 text-red-600" />
            </div>
            <CardTitle className="text-xl font-semibold text-gray-900">
              Access Denied
            </CardTitle>
            <CardDescription className="text-gray-600">
              You don't have permission to access this page.
            </CardDescription>
          </CardHeader>
          <CardContent className="text-center">
            <div className="space-y-2 text-sm text-gray-500">
              {requireSuperAdmin && (
                <p>This page requires super administrator privileges.</p>
              )}
              {permission && (
                <p>Required permission: <code className="bg-gray-100 px-1 rounded">{permission}</code></p>
              )}
              {permissions && permissions.length > 0 && (
                <p>
                  Required permissions ({requireAll ? 'all' : 'any'}): {' '}
                  {permissions.map(p => (
                    <code key={p} className="bg-gray-100 px-1 rounded mr-1">{p}</code>
                  ))}
                </p>
              )}
              {role && (
                <p>Required role: <code className="bg-gray-100 px-1 rounded">{role}</code></p>
              )}
              {roles && roles.length > 0 && (
                <p>
                  Required roles ({requireAllRoles ? 'all' : 'any'}): {' '}
                  {roles.map(r => (
                    <code key={r} className="bg-gray-100 px-1 rounded mr-1">{r}</code>
                  ))}
                </p>
              )}
            </div>
            <div className="mt-6">
              <button
                onClick={() => window.history.back()}
                className="text-blue-600 hover:text-blue-500 font-medium"
              >
                Go Back
              </button>
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }

  // If all checks pass, render the protected content
  return <>{children}</>;
};

export default PermissionRoute;
